@use "./utils";

.presentation {
    height: 100vh;
    padding: utils.getSize(1);
    background-color: #00000f;
    pointer-events: none;
    overflow: hidden;

    .logo_container {
        position: absolute;
        width: 60%;
        height: 100%;
        top: 0;
        left: 0;
        background: linear-gradient(100deg, #000000 0%, #000020 80%);
        display: grid;
        place-items: center;
        clip-path: polygon(0% 0%, 100% 0%, 65% 100%, 0% 100%);
        @include utils.vertical {
            width: 100%;
            height: 60%;
            clip-path: polygon(0% 0%, 100% 0%, 100% 75%, 0% 100%);
        }

        .logo {
            width: utils.getSize(29);
            aspect-ratio: 1 / 1;
            position: relative;
            border-radius: 50%;
            display: block;
            margin-right: utils.getSize(5);
            @include utils.vertical {
                margin-right: unset;
                margin-bottom: utils.getSize(5);
            }

            > * {
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                width: 100%;
                height: 100%;
                aspect-ratio: 1 / 1;
            }

            img {
                border-radius: 50%;
            }

            svg {
                &.lineart {
                    filter: drop-shadow(0 0 5px white) blur(utils.getSize(1));
                }
            }
        }
    }

    .content_container {
        position: absolute;
        right: 0;
        width: 65%;
        background-color: #050509;
        color: white;
        font-family: 'Poppins';
        height: 100%;
        z-index: 0;
        @include utils.vertical {
            right: unset;
            bottom: 0;
            width: 100%;
            height: 55%;
        }

        .content_main {
            position: relative;
            width: 100%;
            height: 100%;
            display: grid;
            place-items: center;

            .decoration_overlap {
                position: absolute;
                width: 200%;
                height: 150%;
                right: -25%;
                top: -25%;
                background-color: utils.$accent-color;
                display: grid;
                place-items: center;

                .content_centred {
                    right: 12.85%;
                    margin-top: .5%;

                    .title {
                        color: white;
                        // margin-bottom: utils.getSize(.7);
                    }

                    .name_container .text {
                        color: transparent;
                        -webkit-text-stroke: white 1px;
                        font-weight: 700;
                    }

                    .comment {
                        opacity: 0;
                    }
                }
            }

            .content_centred {
                position: absolute;
                right: 0;
                width: max-content;
                display: flex;
                flex-direction: column;
                align-items: flex-end;
                padding-right: utils.getSize(2);

                .title {
                    font-size: utils.getSize(5);
                    top: 20%;
                    // color: hsl(0 100 100 / .125);
                    color: transparent;
                    -webkit-text-stroke-color: white;
                    -webkit-text-stroke-width: utils.getSize(0.01);
                    @include utils.firefox {
                        -webkit-text-stroke-width: utils.getSize(0.1);
                    }
                    font-weight: 900;
                    font-family: 'Poetsen One';
                    margin-bottom: utils.getSize(1);
                    @include utils.vertical {
                        font-size: utils.getSize(11, 1);
                    }
                }

                .name_container {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    width: max-content;
                    padding-right: utils.getSize(3);

                    .text {
                        text-wrap: nowrap;
                        position: relative;
                        font-weight: 700;
                    }
                }

                .name {
                    font-size: utils.getSize(4.4);
                    margin: utils.getSize(-1.5);
                    @include utils.vertical {
                        font-size: utils.getSize(8.5, 1);
                        margin: utils.getSize(-1.5, 1);
                    }
                }

                .comment {
                    font-style: italic;
                    opacity: 0.6;
                    font-size: utils.getSize(.9);
                    padding-bottom: utils.getSize(5);
                    color: utils.$accent-text;
                    @include utils.vertical {
                        font-size: utils.getSize(1.8, 1);
                    }
                }
            }
        }

        .content_background {
            position: absolute;
            width: 100%;
            height: 100%;
            right: 0;
            top: 0;
            bottom: 0;
            left: 0;
            background-image: url("../../public/assets/abstract-1.jpg");
            background-size: cover;
            background-position: center;
            opacity: 0.6;

            &_filter {
                position: absolute;
                width: 100%;
                height: 100%;
                top: 0;
                left: 0;
                bottom: 0;
                right: 0;
                backdrop-filter: blur(utils.getSize(0.4));
            }
        }
    }
}
