@use "./utils";
@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poetsen+One&display=swap');

html {
    min-width: 400px;
    min-height: 540px;
    width: 100vw;
    height: 100vh;
    height: 100dvh;
    overflow: hidden;
}
*::-webkit-scrollbar {
    width: 0;
}
::-webkit-scrollbar {
    width: 0;
    height: 0;
    display: none;
}

body {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-rendering: geometricPrecision;
    font-family:
        'Poppins',
        -apple-system,
        BlinkMacSystemFont,
        Segoe UI,
        Roboto,
        Oxygen,
        Ubuntu,
        Cantarell,
        Fira Sans,
        Droid Sans,
        Helvetica Neue,
        sans-serif;
    background: black;
    color: white;
    font-size: utils.getSize(1);
    width: 100%;
    height: 100vh;
    height: 100dvh;
    overflow-y: overlay;
    overflow-x: hidden;
}

section {
    min-height: 100vh;
    position: relative;
    scroll-snap-align: start;
    @include utils.grid;
}

*,
*::after,
*::before {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.app-container {
    width: 100vw;
    height: 100vh;
    scroll-snap-type: y mandatory;
    position: relative;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    overflow-y: auto;
    overflow-x: hidden;
    font-size: utils.getSize(1);
    margin-inline: auto;
    scrollbar-width: none;
    // pointer-events: none;

    &::-webkit-scrollbar {
        display: none;
    }
}

.skill {
    position: relative;
    display: flex;
    flex-direction: row;
    gap: utils.getSize(1);
    // background-color: lime;

    &_icon {
        position: relative;
        display: block;
        width: utils.getSize(5);
        height: utils.getSize(5);

        svg {
            display: block;
            width: 100% !important;
            height: 100% !important;
            aspect-ratio: 1/1;
        }
    }

    &_content {
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;

        // .skill_level {
        //     display: flex;
        //     flex-direction: row;
        // }
    }

    &_name {
        position: relative;
        font-size: utils.getSize(1.5);
    }

    &_level {
        display: flex;
        flex-direction: row;
        &_star {
            display: inline-block;

            &_outlined,
            &_fill_container {
                position: relative;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                width: 100%;
                height: 100%;
            }

            &_fill_star {
                
            }
        }
    }
}

@import url("./top.scss");
