$scaling-unit: min(max(1vw, 1vh), 2vw, 2vh);
$mobile-scaling-unit: min(1vw, 1vh);

$accent-text: hsl(105, 100%, 35%);
$accent-color: hsl(340, 40%, 40%);

@function getSize($multiplier: 1, $vertical: 0) {
    @if $vertical == 1 {
        @return #{calc($mobile-scaling-unit * $multiplier)};
    }
    @return #{calc($scaling-unit * $multiplier)};
}

@mixin grid {
    display: grid;
    place-items: center;
}

@mixin vertical {
    .vertical & {
        @content;
    }
}

@mixin firefox {
    @-moz-document url-prefix() {
        @content;
    }
}
